<template>
  <div class="leaderboard">
    <div>
      <h1 class="text-center text-white text-2xl font-bold mb-4">
        Leaderboard-{{ this.$route.params.id }}
      </h1>
      <div
        class="md:w-1/2 md:mx-auto mx-1 rounded p-5 bg-gray-50 bg-opacity-40"
      >
        <div>
          <input
            v-model="search"
            placeholder="Search by Name/College"
            @keyup="searchResult"
            class="input-block"
          />
        </div>
        <div class="mt-4">
          <div v-if="allresult.length > 0">
            <div
              v-for="(r, i) in testResults"
              :key="i"
              :class="`${getborderStyle(
                r.position
              )} flex justify-between items-center p-5 rounded bg-gray-100 shadow-md`"
            >
              <span
                class="
                  h-10
                  w-10
                  text-white
                  bg-gradient-to-t
                  from-red-400
                  to-red-600
                  rounded-full
                  flex
                  justify-center
                  items-center
                  font-semibold
                "
              >
                {{ r.position }}
              </span>
              <div class="flex-1 ml-2 md:ml-4">
                <h2 class="text-lg font-semibold">
                  {{ r.name }}
                </h2>
                <p>{{ r.duration }} minutes</p>
                <p>{{ r.school }}</p>
              </div>
              <div class="p-2 border-2 border-red-400">
                {{ r.score }}
              </div>
            </div>
          </div>
          <div v-else class="text-center">
            <loader />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      search: "",
      testResults: [],
      allresult: [],
      results: [],
    };
  },

  methods: {
    modeltestResults() {
      fetch(
        `https://script.google.com/macros/s/AKfycbwvwOv6OKjK8qTJdFcQF4v_AthQddTji1NEJhinBkD1XfvDggrYW8Dn4rBsXNNne61ktg/exec?id=${this.$route.params.id}`
      )
        .then((res) => res.json())
        .then((data) => {
          fetch(data.exam.question)
            .then((res) => res.json())
            .then((d) => {
              let testResults = d;
              this.testResults = testResults
                .sort((c, d) => c.duration - d.duration)
                .sort((a, b) => b.score - a.score)
                .map((r, i) => ({
                  position: i + 1,
                  name: r.name,
                  school: r.school,
                  duration: (r.duration / 60000).toFixed(2),
                  score: r.score,
                }));

              this.allresult = this.testResults;
            });
        });
    },
    searchResult() {
      this.testResults = this.allresult.filter((r) => {
        if (
          r.school.toLowerCase().includes(this.search.toLowerCase()) ||
          r.name.toLowerCase().includes(this.search.toLowerCase())
        ) {
          return r;
        }
      });
    },
    getStyle(p) {
      if (p == 1) {
        return "success";
      } else if (p == 2) {
        return "orange darken-2";
      } else if (p == 3) {
        return "indigo";
      }
      return "";
    },
    getborderStyle(p) {
      if (p == 1) {
        return "s singleResult mb-2";
      } else if (p == 2) {
        return "f singleResult mb-2";
      } else if (p == 3) {
        return "d singleResult mb-2";
      }
      return "singleResult mb-2";
    },
  },

  created() {
    this.modeltestResults();
    window.scrollTo(0, 0);
  },
};
</script>

<style lang="scss" scoped>
.leaderboard {
  background-attachment: fixed;
  min-height: 100vh;
  .resultcontainer {
    background: rgba(223, 221, 221, 0.747) !important;
    box-shadow: 0 8px 32px 0 rgba(12, 13, 32, 0.479);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.18);
    .singleResult {
      background: rgba(255, 255, 255, 0.35);
      box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
      backdrop-filter: blur(18.5px);
      -webkit-backdrop-filter: blur(18.5px);
      border-radius: 10px;
      border: 1px solid rgba(255, 255, 255, 0.18);
    }
  }
}

.s {
  border: 2px solid rgba(22, 223, 49, 0.808) !important;
}
.f {
  border: 2px solid rgba(231, 154, 10, 0.808) !important;
}
.d {
  border: 2px solid rgba(23, 110, 192, 0.808) !important;
}
.totalmarks {
  border: 2px solid rgb(236, 25, 88) !important;
  height: 35px;
  width: 35px;
  padding: 3px;
  box-shadow: 2px 2px 4px rgba(236, 25, 88, 0.8);
}
</style>